.form {
  display: flex;
  align-items: center;
  margin: 20px;
  width: 600px;
}

.label {
  display: flex;
  align-items: center;
  margin-right: 10px;
}
.input {
  margin: 5px;
}
.addBtn {
  padding: 2px;
  background-color: darkblue;
  border: 1px solid black;
  border-radius: 4px;
  box-shadow: -5px -5px 5px -5px rgba(34, 60, 80, 0.6);
  color: aliceblue;
  margin-left: auto;
}
.inputTitle {
  color: blue;
}
